<template>
  <div>
    <h2>我是用户界面</h2>
    <p>我是用户的相关信息，嘻嘻</p>
    <h2>用户名：{{userId}}</h2>
    <h2>用户名：{{$route.params.id}}</h2>
  </div>
</template>

<script>
    export default {
        name: "User",
        computed:{
          userId(){
            return this.$route.params.id
          },
        }
    }
</script>

